.div16 {
  position: relative;
  font-size: var(--font-size-base);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: left;
}
.button-create {
  cursor: pointer;
  border: 0;
  padding: var(--padding-mini) 80px;
  background-color: var(--color-black);
  align-self: stretch;
  border-radius: var(--br-mini);
  height: 50px;
  flex-direction: row;
  box-sizing: border-box;
}
.button-1,
.button-create,
.div17 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.div17 {
  width: 316px;
  position: relative;
  font-size: var(--font-size-sm);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  flex-shrink: 0;
}
.button-1 {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-34xl) var(--padding-2xl);
  background-color: rgba(207, 126, 236, 0.36);
  align-self: stretch;
  border-radius: var(--br-3xs);
  flex-direction: row;
}
.div18 {
  width: 320px;
  position: relative;
  font-size: var(--font-size-sm);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  flex-shrink: 0;
}
.button-2,
.div18,
.div19 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.button-2 {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-34xl) var(--padding-lgi);
  background-color: rgba(82, 212, 255, 0.36);
  align-self: stretch;
  border-radius: var(--br-3xs);
  flex-direction: row;
}
.div19 {
  width: 334px;
  position: relative;
  font-size: var(--font-size-sm);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
  flex-shrink: 0;
}
.button-1-parent,
.button-3 {
  align-self: stretch;
  display: flex;
}
.button-3 {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: 44px var(--padding-xs);
  background-color: rgba(236, 126, 126, 0.36);
  border-radius: var(--br-3xs);
  box-sizing: border-box;
  height: 140px;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.button-1-parent {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-2xs);
}
.frame2 {
  align-self: stretch;
  align-items: center;
}
.button-create-parent,
.frame2,
.main1 {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.button-create-parent {
  align-self: stretch;
  align-items: center;
  gap: var(--gap-3xs);
}
.main1 {
  width: 360px;
  align-items: flex-end;
  gap: var(--gap-xs);
}
.main-wrapper {
  position: absolute;
  top: 17.5px;
  left: calc(50% - 190px);
  width: 380px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs);
  box-sizing: border-box;
}
.generate-screen {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  height: 844px;
}
