.c {
  position: relative;
  font-size: var(--font-size-sm);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.c-wrapper {
  cursor: pointer;
  border: 0;
  padding: var(--padding-mini) var(--padding-76xl);
  background-color: var(--color-black);
  width: 366px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-lg);
  height: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.gem-stone-1-1-icon {
  width: 120px;
  position: relative;
  height: 120px;
}
.div20 {
  position: relative;
  font-size: var(--font-size-5xl);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: left;
  text-shadow: 0.3px 0 0#000, 0 0.3px 0#000, -0.3px 0 0#000, 0-0.3px 0#000;
}
.button-premium {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: 0 var(--padding-125xl);
  background-color: var(--color-lightskyblue-100);
  width: 366px;
  border-radius: var(--br-xl);
  box-sizing: border-box;
  height: 189px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.speech-balloon-1 {
  align-self: stretch;
  flex: 1;
  position: relative;
}
.button-support,
.main2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.button-support {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-11xl) var(--padding-102xl) var(--padding-4xl);
  background-color: var(--color-lightpink-100);
  width: 366px;
  border-radius: var(--br-xl);
  box-sizing: border-box;
  height: 189px;
}
.main2 {
  align-self: stretch;
  gap: var(--gap-8xl);
}
