.div12,
.div13 {
  position: relative;
  font-weight: 900;
}
.div12 {
  width: 295px;
  display: inline-block;
}
.div13 {
  font-size: var(--font-size-13xl);
  font-family: var(--font-montserrat);
  color: var(--color-black);
  text-align: center;
}
.wrapper {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-5xl) var(--padding-50xl);
  background-color: var(--color-white);
  align-self: stretch;
  border-radius: var(--br-xl);
  box-sizing: border-box;
  height: 96px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.div14 {
  position: relative;
  font-size: var(--font-size-13xl);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: center;
}
.frame-button {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-5xl) var(--padding-50xl);
  background-color: var(--color-black);
  align-self: stretch;
  border-radius: var(--br-xl);
  box-sizing: border-box;
  height: 96px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.div15 {
  position: relative;
  font-size: 25px;
  font-weight: 900;
  font-family: var(--font-montserrat);
  background: linear-gradient(90deg, #cf7eec, #ffa2a2 53%, #52d4ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
}
.frame-parent1,
.wrapper1 {
  align-self: stretch;
  display: flex;
}
.wrapper1 {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: 33px 43px;
  background-color: var(--color-black);
  border-radius: var(--br-xl);
  box-sizing: border-box;
  height: 96px;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.frame-parent1 {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-mid);
}
.parent1,
.th-page1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.parent1 {
  width: 309px;
  gap: var(--gap-mid);
}
.th-page1 {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  height: 844px;
  overflow: hidden;
  padding: 65px 0;
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
