.frame-icon {
  width: 70px;
  border-radius: var(--br-3xs);
  height: 86px;
}
.div26,
.pdf {
  align-self: stretch;
  position: relative;
  text-align: left;
}
.pdf {
  font-size: var(--font-size-xl);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-black);
}
.div26 {
  font-size: var(--font-size-xs);
  font-family: var(--font-montserrat-alternates);
  color: var(--color-gray);
  display: inline-block;
  height: 89px;
  flex-shrink: 0;
}
.frame-wrapper,
.pdf-parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.pdf-parent {
  align-self: stretch;
  gap: var(--gap-12xs);
}
.frame-wrapper {
  width: 195px;
}
.frame-parent3 {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-11xl) var(--padding-lg) var(--padding-7xs)
    var(--padding-4xl);
  background-color: var(--color-white);
  align-self: stretch;
  border-radius: var(--br-3xs);
  box-sizing: border-box;
  height: 153px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-36xl);
}
