.h13 {
  align-self: stretch;
  position: relative;
  line-height: 100%;
  font-weight: 900;
}
.buttom-slide {
  width: 171px;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  border: 0.3px solid var(--color-black);
  box-sizing: border-box;
  height: 46px;
  display: flex;
  justify-content: center;
  padding: var(--padding-2xs) var(--padding-17xl) var(--padding-smi);
  gap: var(--gap-3xs);
  font-family: var(--font-montserrat);
  font-weight: 900;
  font-size: var(--font-size-xs);
  color: var(--color-black);
}
.buttom-slide,
.buttom-slide-parent,
.frame-select {
  flex-direction: row;
  align-items: center;
}
.frame-select {
  width: 171px;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  border: 0.3px solid var(--color-black);
  box-sizing: border-box;
  height: 46px;
  display: flex;
  justify-content: center;
  padding: var(--padding-2xs) var(--padding-18xl) var(--padding-3xs);
  gap: var(--gap-3xs);
  font-family: var(--font-montserrat);
  font-weight: 900;
  font-size: var(--font-size-xs);
  color: var(--color-black);
}
.buttom-slide-parent {
  align-self: stretch;
  gap: var(--gap-5xs);
}
.buttom-slide-parent,
.h1-container,
.input {
  display: flex;
  justify-content: center;
}
.h1-container {
  align-self: stretch;
  flex-direction: column;
  align-items: center;
  gap: var(--gap-xl);
}
.input {
  border: 0.3px solid var(--color-black);
  outline: 0;
  background-color: var(--color-white);
  width: 352px;
  border-radius: var(--br-3xs);
  box-sizing: border-box;
  height: 100px;
  flex-direction: row;
  align-items: flex-start;
  padding: var(--padding-sm) var(--padding-2xl) var(--padding-3xl);
  font-family: var(--font-montserrat);
  font-weight: 900;
  font-size: var(--font-size-base);
  color: var(--color-black);
}
.generate-conainer,
.generate-conainer-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.generate-conainer {
  gap: var(--gap-4xs);
}
.generate-conainer-wrapper {
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
