.button-back-child {
  width: 34px;
  position: relative;
  max-height: 100%;
  object-fit: contain;
}
.button-back {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: 16px var(--padding-sm);
  background-color: var(--color-white);
  width: 62px;
  border-radius: var(--br-xl);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.div6,
.div8 {
  position: relative;
  font-weight: 900;
}
.div6 {
  align-self: stretch;
}
.div8 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.button-month,
.button-week {
  width: 100px;
  border-radius: var(--br-3xs);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  height: 28px;
  padding: var(--padding-7xs) var(--padding-xs);
}
.button-week {
  background-color: rgba(207, 126, 236, 0.3);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.button-month {
  background-color: rgba(82, 212, 255, 0.5);
}
.button-month,
.button-year,
.div7 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.button-year {
  width: 100px;
  border-radius: var(--br-3xs);
  background-color: rgba(255, 162, 162, 0.7);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  height: 28px;
  padding: var(--padding-7xs) var(--padding-xs);
}
.div7 {
  align-self: stretch;
  gap: 7px;
  font-size: var(--font-size-xs);
}
.button-free-parent,
.frame-container,
.frame-div {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.frame-div {
  align-items: center;
  padding: 0 var(--padding-smi);
  gap: var(--gap-2xs);
}
.button-free-parent,
.frame-container {
  align-items: flex-start;
  gap: var(--gap-4xl);
}
.frame-container {
  align-items: center;
  padding: 0 3.5px;
}
.div11 {
  position: relative;
  font-weight: 900;
}
.button-subscribe {
  align-self: stretch;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-mini);
  background-color: var(--color-black);
  border: 1px solid var(--color-black);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 20px 85px;
  cursor: pointer;
  font-size: var(--font-size-sm);
  color: var(--color-white);
}
.button-back-parent,
.frame1,
.premium {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.frame1 {
  align-self: stretch;
  align-items: center;
  gap: 33px;
}
.button-back-parent,
.premium {
  width: 347px;
  align-items: flex-start;
}
.premium {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  height: 844px;
  overflow: hidden;
  align-items: center;
  padding: 32px 0;
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
