.div22,
.div23 {
  align-self: stretch;
  position: relative;
  font-weight: 900;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.div23 {
  font-size: var(--font-size-mini);
  color: var(--color-black);
  margin-top: -2px;
}
.frame-parent2,
.parent2 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame-parent2 {
  gap: var(--gap-mini);
}
.div25 {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-mini);
  font-weight: 900;
  color: var(--color-black);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.frame3 {
  width: 301px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-12xl);
  z-index: 0;
}
.button-free,
.rectangle-input {
  border: 1px solid var(--color-black);
  box-sizing: border-box;
}
.rectangle-input {
  margin: 0 !important;
  width: 35px;
  position: absolute;
  top: 19px;
  left: 283px;
  border-radius: var(--br-3xs);
  height: 35px;
  z-index: 1;
}
.button-free {
  align-self: stretch;
  border-radius: var(--br-mini);
  background-color: var(--color-white);
  height: 160px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-lgi) var(--padding-lg);
  position: relative;
  gap: var(--gap-3xs);
  text-align: left;
  font-size: var(--font-size-xs);
  color: var(--color-dimgray);
  font-family: var(--font-montserrat);
}
