@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-montserrat: Montserrat;
  --font-montserrat-alternates: "Montserrat Alternates";
  --font-inter: Inter;

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-13xl: 32px;
  --font-size-xl: 20px;
  --font-size-smi: 13px;
  --font-size-mini: 15px;
  --font-size-xs: 12px;
  --font-size-9xl: 28px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-lightpink-100: #ffa2a2;
  --color-lightskyblue-100: #52d4ff;
  --color-darkgray-100: #969696;
  --color-dimgray: #676767;
  --color-gray: #7c7c7c;

  /* Gaps */
  --gap-8xl: 27px;
  --gap-12xs: 1px;
  --gap-2xs: 11px;
  --gap-xs: 12px;
  --gap-3xs: 10px;
  --gap-4xl: 23px;
  --gap-12xl: 31px;
  --gap-mini: 15px;
  --gap-mid: 17px;
  --gap-sm: 14px;
  --gap-7xl: 26px;
  --gap-36xl: 55px;
  --gap-4xs: 9px;
  --gap-xl: 20px;
  --gap-5xs: 8px;

  /* Paddings */
  --padding-xs: 12px;
  --padding-11xl: 30px;
  --padding-102xl: 121px;
  --padding-4xl: 23px;
  --padding-125xl: 144px;
  --padding-mini: 15px;
  --padding-76xl: 95px;
  --padding-8xs: 5px;
  --padding-8xl: 27px;
  --padding-41xl: 60px;
  --padding-33xl: 52px;
  --padding-lgi: 19px;
  --padding-lg: 18px;
  --padding-smi: 13px;
  --padding-7xs: 6px;
  --padding-sm: 14px;
  --padding-5xl: 24px;
  --padding-50xl: 69px;
  --padding-3xs: 10px;
  --padding-34xl: 53px;
  --padding-2xl: 21px;
  --padding-3xl: 22px;
  --padding-2xs: 11px;
  --padding-18xl: 37px;
  --padding-17xl: 36px;

  /* Border radiuses */
  --br-xl: 20px;
  --br-lg: 18px;
  --br-mini: 15px;
  --br-3xs: 10px;
}
