.h1 {
  position: relative;
  font-weight: 900;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.h1,
.h1-wrapper,
.h2 {
  align-self: stretch;
}
.h1-wrapper {
  height: 45px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.h2 {
  margin: 0;
  position: relative;
  font-size: var(--font-size-base);
  font-weight: 400;
  font-family: var(--font-montserrat-alternates);
  color: #aeaeae;
}
.frame-parent {
  align-self: stretch;
  flex-direction: column;
  gap: var(--gap-12xs);
}
.frame-parent,
.indexhtml,
.main {
  display: flex;
  align-items: center;
  justify-content: center;
}
.main {
  height: 635px;
  flex-direction: column;
  gap: 30px;
}
.indexhtml {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  height: 844px;
  overflow: hidden;
  flex-direction: row;
  padding: 61px var(--padding-xs);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
