.gif-icon,
.h11 {
  position: relative;
}
.h11 {
  align-self: stretch;
  font-weight: 900;
}
.gif-icon {
  width: 168px;
  height: 174px;
}
.h1-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 25px;
}
.div {
  position: relative;
  font-size: var(--font-size-base);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-white);
  text-align: left;
}
.button-index-html,
.frame-group,
.th-page {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.button-index-html {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--color-black);
  width: 360px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-lg);
  height: 60px;
  justify-content: center;
}
.frame-group,
.th-page {
  justify-content: flex-start;
}
.frame-group {
  align-self: stretch;
  gap: 77px;
}
.th-page {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  overflow: hidden;
  padding: 180px 0;
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
